@media (min-width: 200px) {
  .landingPage_container {
    height: 100vh;
    background-color: black;
    background-image: url(../assets/LandingPage.jfif);
    width: 100vw;
    height: 100vh;
    background-repeat: no-repeat;
    background-size: 35%;
    object-fit: cover;
    background-position: 50% 10%;

    display: flex;
    justify-content: center;
    align-items: center;
  }
}

